.nav {
  &__list {
    display: flex;
    height: 60px;

    align-items: center;
    text-transform: uppercase;
    font-size: 12px;
  }

  &__item {
    position: relative;
    height: 60px;
    margin-right: 20px;
    list-style-type: none;
  }

  &__item:last-of-type {
    margin-right: 0;
  }
  &__link {
    display: inline-block;
    padding: 23px 0;
    color: black;
    text-decoration: none;
  }
}

.nav__link--is-active,
.nav__link:hover {
  color: #00acdc;
}

.nav__link--is-active:after {
  content: "";
  width: 37px;
  height: 4px;
  display: block;
  background-color: #00acdc;
  border-radius: 8px;
  position: absolute;
  bottom: 0;
}
